:root {
  --primary-color: #4a6da7;
  --secondary-color: #ff6b6b;
  --accent-color: #ffd166;
  --text-color: #333;
  --bg-color: #f9f9f9;
  --card-bg: #fff;
  --correct-color: #6bcd69;
  --incorrect-color: #ff6b6b;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: var(--bg-color);
  color: var(--text-color);
  font-family: Noto Sans TC, 微軟正黑體, sans-serif;
  line-height: 1.6;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem;
}

header {
  background-color: var(--primary-color);
  color: #fff;
  text-align: center;
  margin-bottom: 2rem;
  padding: 1rem 0;
}

.quiz-card {
  background-color: var(--card-bg);
  border-radius: 8px;
  margin-bottom: 2rem;
  padding: 2rem;
  box-shadow: 0 4px 6px #0000001a;
}

.question {
  margin-bottom: 1.5rem;
  font-size: 1.2rem;
  font-weight: 600;
}

.options {
  flex-direction: column;
  gap: 1rem;
  display: flex;
}

.option {
  cursor: pointer;
  border: 1px solid #ddd;
  border-radius: 4px;
  padding: 1rem;
  transition: background-color .3s;
}

.option:hover {
  background-color: #f0f0f0;
}

.option.selected {
  border-color: var(--primary-color);
  background-color: #4a6da71a;
}

.option.correct {
  background-color: var(--correct-color);
  color: #fff;
  border-color: var(--correct-color);
}

.option.incorrect {
  background-color: var(--incorrect-color);
  color: #fff;
  border-color: var(--incorrect-color);
}

.solution {
  border-left: 4px solid var(--primary-color);
  background-color: #f8f9fa;
  margin-top: 1.5rem;
  padding: 1rem;
  display: none;
}

.solution.show {
  display: block;
}

.button {
  background-color: var(--primary-color);
  color: #fff;
  cursor: pointer;
  border: none;
  border-radius: 4px;
  padding: .8rem 1.5rem;
  font-size: 1rem;
  text-decoration: none;
  transition: background-color .3s;
  display: inline-block;
}

.button:hover {
  background-color: #3a5a8f;
}

.button-secondary {
  background-color: var(--secondary-color);
}

.button-group {
  gap: 1rem;
  margin-top: 2rem;
  display: flex;
}

.category-list {
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 1.5rem;
  margin-bottom: 2rem;
  display: grid;
}

.category-card {
  background-color: var(--card-bg);
  text-align: center;
  cursor: pointer;
  border-radius: 8px;
  padding: 1.5rem;
  transition: transform .3s, box-shadow .3s;
  box-shadow: 0 4px 6px #0000001a;
}

.category-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 6px 10px #00000026;
}

.results {
  text-align: center;
  margin-top: 2rem;
}

.score {
  color: var(--primary-color);
  margin-bottom: 1rem;
  font-size: 2rem;
  font-weight: bold;
}

.progress-bar {
  background-color: #ddd;
  border-radius: 4px;
  height: 8px;
  margin-bottom: 2rem;
  overflow: hidden;
}

.progress {
  background-color: var(--primary-color);
  height: 100%;
  transition: width .3s;
}

.loading-container, .error-container {
  text-align: center;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 80vh;
  padding: 2rem;
  display: flex;
}

.loading {
  color: var(--primary-color);
  margin-bottom: 1rem;
  font-size: 1.5rem;
  font-weight: bold;
}

.loading-text {
  color: #666;
  margin-bottom: 2rem;
}

@keyframes pulse {
  0% {
    opacity: .6;
  }

  50% {
    opacity: 1;
  }

  100% {
    opacity: .6;
  }
}

.loading:after {
  content: "...";
  animation: 1.5s infinite pulse;
}

.error {
  color: var(--incorrect-color);
  margin-bottom: 1rem;
  font-size: 1.5rem;
  font-weight: bold;
}

.error-container p {
  color: #666;
  max-width: 600px;
  margin-bottom: 2rem;
}

.admin-panel {
  max-width: 800px;
  margin: 0 auto;
}

.upload-section {
  background-color: var(--card-bg);
  border-radius: 8px;
  margin-bottom: 2rem;
  padding: 2rem;
  box-shadow: 0 4px 6px #0000001a;
}

.file-input {
  margin: 1.5rem 0;
}

.success-message {
  color: var(--correct-color);
  background-color: #6bcd691a;
  border-radius: 4px;
  margin: 1rem 0;
  padding: .5rem;
  font-weight: bold;
}

.error-message {
  color: var(--incorrect-color);
  background-color: #ff6b6b1a;
  border-radius: 4px;
  margin: 1rem 0;
  padding: .5rem;
  font-weight: bold;
}

.preview-section {
  background-color: var(--card-bg);
  border-radius: 8px;
  padding: 2rem;
  box-shadow: 0 4px 6px #0000001a;
}

.data-preview {
  background-color: #f8f9fa;
  border: 1px solid #eee;
  border-radius: 4px;
  max-height: 400px;
  margin-top: 1rem;
  padding: 1rem;
  overflow-y: auto;
}

.data-preview ul {
  padding-left: 1rem;
  list-style-type: none;
}

.data-preview li {
  margin-bottom: .5rem;
}

.data-preview ul ul {
  border-left: 2px solid #ddd;
  margin-top: .5rem;
  padding-left: 1.5rem;
}

@media (width <= 768px) {
  .container {
    padding: 1rem;
  }

  .category-list {
    grid-template-columns: 1fr;
  }

  .button-group {
    flex-direction: column;
  }
}
